import React from 'react';
import discord from "../../assets/images/icons/discord.svg";
import twitter from "../../assets/images/icons/twitter.svg";
import instagram from "../../assets/images/icons/instagram.svg";
import logo from "../../assets/images/logo.png";
import menu from "../../assets/images/menu.png";

const Footer = () => {
    return (
        <div className='footer'>
            <div className="container">
                <div className="logo"><img src={logo} alt="logo"/></div>
                <nav>
                    <a href="#space" >Apes Of Space VX</a>
                    {/*<a href="#space">Apes Of Space</a>*/}
                    <a href="#lady">Lady Apes</a>
                    <a href="#roadmap">Roadmap</a>
                    {/*<a href="#faq">FAQ</a>*/}
                    <a href="#team">Team</a>
                    <a href="#investors">For Investors</a>
                    <a href="#about">About</a>
                </nav>
                <div className="socials">
                    {/*<a href="#" target='_blank' rel='noopener noopener noreferrer'>*/}
                    {/*    <img src={reddit} alt=""/>*/}
                    {/*</a>*/}
                    <a href="https://discord.com/invite/rRrnHCQsvn" target='_blank' rel='noopener noopener noreferrer'>
                        <img src={discord} alt=""/>
                    </a>
                    {/*<a href="#" target='_blank' rel='noopener noopener noreferrer'>*/}
                    {/*    <img src={medium} alt=""/>*/}
                    {/*</a>*/}
                    {/*<a href="#" target='_blank' rel='noopener noopener noreferrer'>*/}
                    {/*    <img src={telegram} alt=""/>*/}
                    {/*</a>*/}
                    <a href="https://twitter.com/apesofspace_nft " target='_blank' rel='noopener noopener noreferrer'>
                        <img src={twitter} alt=""/>
                    </a>
                    <a href="https://www.instagram.com/apesofspace_nft/" target='_blank' rel='noopener noopener noreferrer'>
                        <img src={instagram} alt=""/>
                    </a>
                </div>
                <button className="connectButton">Connect</button>
                <button className='mobileOpenButton' onClick={()=>{setMenuOpen(true)}}>
                    <img src={menu} alt="mobile button"/>
                </button>
            </div>
        </div>
    );
};

export default Footer;