import React, {useEffect} from 'react';
import Home from "./pages/home";
import AOS from "aos";
import "aos/dist/aos.css";

const App = () => {
    useEffect(() => {
        AOS.init({
            duration : 1000
        });
    }, []);
    return (
        <div>
            <Home/>
        </div>
    );
};

export default App;